import classnames from 'classnames';

const {registerBlockType} = wp.blocks;
const {BlockControls, PlainText, InspectorControls, PanelColorSettings, ContrastChecker, getColorClassName} = wp.editor;
const {RawHTML, Fragment, SandBox} = wp.element;
const {withState} = wp.compose;
const {__} = wp.i18n;

/**
 * ラッパークラス
 */
const wrapClass = classnames(
    'wp-block-button',
    'ystdb-btn-wrap'
);

const defaultStyles = `
    html,body,:root {
        margin: 0 !important;
        padding: 0 !important;
        overflow: visible !important;
        min-height: auto !important;
    }
`;

const edit = ({attributes, setAttributes, className, setState, isPreview}) => {
    console.log(attributes);
    return (
        <Fragment>
            <div className={wrapClass}>
                <BlockControls>
                    <div className="components-toolbar">
                        <button
                            className={`components-tab-button ${!isPreview ? 'is-active' : ''}`}
                            onClick={() => {
                                setState({isPreview: false})
                            }}
                        >
                            <span>HTML</span>
                        </button>
                        <button
                            className={`components-tab-button ${isPreview ? 'is-active' : ''}`}
                            onClick={() => {
                                setState({isPreview: true})
                            }}
                        >
                            <span>{__('Preview')}</span>
                        </button>
                    </div>
                </BlockControls>
                {(isPreview) ? (
                    <SandBox html={attributes.content} styles={defaultStyles}/>
                ) : (
                    <PlainText
                        value={attributes.content}
                        onChange={(value) => setAttributes({content: value})}
                        placeholder={'アフィリエイトタグを入力'}
                        aria-label={__('HTML')}
                    />
                )}
            </div>
        </Fragment>
    );
}

registerBlockType('ystdb/afi-btn', {
    title: 'アフィリエイトリンクボタンブロック',
    description: 'アフィリエイトリンクなど、入力したリンクをボタン形式で表示します。',
    icon: 'embed-generic',
    keywords: [__('link'), __('button'), 'button'],
    category: 'ystdb',
    attributes: {
        content: {
            type: "string",
            source: "html"
        }
    },
    edit: withState({isPreview: true})(edit),
    save({attributes}) {
        return (
            <div className={wrapClass}>
                <RawHTML>{attributes.content}</RawHTML>
            </div>
        );
    },
});